/*============================
  Link Button Base Styles
============================*/

/* Estilos base compartidos */
.lb16,
.lb16x > a,
.lb16xx a,
.lb24,
.lb24x > a,
.lb24xx a,
.lb32,
.lb32x > a,
.lb32xx a,
.lb40,
.lb40x > a,
.lb40xx a,
.lb48,
.lb48x > a,
.lb48xx a,
.lb56,
.lb56x > a,
.lb56xx a,
.lb64,
.lb64x > a,
.lb64xx a,
.lb72,
.lb72x > a,
.lb72xx a,
.lb80,
.lb80x > a,
.lb80xx a,
.lb88,
.lb88x > a,
.lb88xx a,
.lb96,
.lb96x > a,
.lb96xx a,
.lb104,
.lb104x > a,
.lb104xx a,
.lb112,
.lb112x > a,
.lb112xx a,
.lb120,
.lb120x > a,
.lb120xx a,
.lb128,
.lb128x > a,
.lb128xx a {
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; /* mismo centrado que los btXX */
  align-items: center;
  text-decoration: none;
  font-size: inherit;
  background-color: inherit;
  color: inherit;
  vertical-align: top;
  cursor: pointer;
  box-sizing: border-box;
  word-wrap: break-word;
}

/* Altura y padding unificado */
.lb16,
.lb16x > a,
.lb16xx a {
  min-height: 16px;
  padding: 0 2px;
}
